.correct-guess {
  animation: correct-pop .6s linear both;
}

@keyframes correct-pop {
  0% {
    box-shadow: 0 0 0 0 hsla(145, 63%, 49%, .85),
                0 0 0px 0px hsla(145, 63%, 49%, .85);
	}
  1% {
    box-shadow: 0 0 0 0 hsla(145, 63%, 49%, .85),
                0 0 10px 3px hsla(145, 63%, 49%, .85);
	}
	66% {
    box-shadow: 0 0 10px 30px hsla(145, 63%, 49%, 0),
                0 0 10px 3px hsla(145, 63%, 49%, .85);
	}
  100% {
    box-shadow: 0 0 10px 30px hsla(145, 63%, 49%, 0),
                0 0 10px 3px hsla(145, 63%, 49%, 0);
  }
}

.incorrect-guess {
  animation: incorrect-pop .6s linear both;
}

@keyframes incorrect-pop {
  0% {
    box-shadow: 0 0 0 0 hsla(7, 63%, 49%, .85),
                0 0 0px 0px hsla(7, 63%, 49%, .85);
	}
  1% {
    box-shadow: 0 0 0 0 hsla(7, 63%, 49%, .85),
                0 0 10px 3px hsla(7, 63%, 49%, .85);
	}
	66% {
    box-shadow: 0 0 10px 30px hsla(7, 63%, 49%, 0),
                0 0 10px 3px hsla(7, 63%, 49%, .85);
	}
  100% {
    box-shadow: 0 0 10px 30px hsla(7, 63%, 49%, 0),
                0 0 10px 3px hsla(7, 63%, 49%, 0);
  }
}