.chord-name-correct-pop {
  animation: chord-name-correct-pop 0.4s cubic-bezier(0.470, 0.000, 0.745, 0.715) both;
}

@keyframes chord-name-correct-pop {
  0% {
    transform: scale(1);
    filter: drop-shadow(0px 0px 0px hsla(145, 63%, 49%, 1));
  }
  50% {
    transform: scale(1.5);
  }
	90% {
    filter: drop-shadow(0px 0px 5px hsla(145, 63%, 49%, 1));
	}
  100% {
    transform: scale(1);
    filter: drop-shadow(0px 0px 5px hsla(145, 63%, 49%, 0));
  }
}

/* 1.25s + .6s waiting for progress bar to fill */
.chord-mastery-pop {
  animation: chord-mastery-pop 1.85s cubic-bezier(0.33, 1, 0.68, 1) both;
}

@keyframes chord-mastery-pop {
  31% {
    box-shadow: 0 0 0 0 hsla(41, 63%, 49%, 0.7),
                0 0 0px 0px hsla(41, 63%, 49%, 0.7);
	}
  32% {
    box-shadow: 0 0 0 0 hsla(41, 63%, 49%, 0.7),
                0 0 10px 3px hsla(41, 63%, 49%, 0.7);
	}
	100% {
    box-shadow: 0 0 10px 20px hsla(41, 63%, 49%, 0),
    0 0 10px 3px hsla(41, 63%, 49%, 0.7);
	}
}