.chord-mastery-glow {
  /* box-shadow: 0 0 10px 8px hsla(41, 63%, 49%, 0.7); */
  animation: chord-mastery-pop .8s linear both;
}

@keyframes chord-mastery-pop {
  50% {
    box-shadow: 0 0 0 0 hsla(41, 63%, 49%, 0.7),
                0 0 0px 0px hsla(41, 63%, 49%, 0.7);
	}
  51% {
    box-shadow: 0 0 0 0 hsla(41, 63%, 49%, 0.7),
                0 0 10px 3px hsla(41, 63%, 49%, 0.7);
    background-color: slategrey;
	}
	100% {
    box-shadow: 0 0 10px 30px hsla(41, 63%, 49%, 0),
    0 0 10px 3px hsla(41, 63%, 49%, 0.7);
    background: linear-gradient(45deg, hsla(41, 63%, 49%, 0.7), white);
	}
}

.chord-mastery-glow::after {
  background: linear-gradient(45deg, hsla(41, 63%, 49%, 0.7), white);
  width: 220px;
  height: 70px;
  content: "";
  border-radius: 10%;
  animation: glow-swipe 0.4s linear both;
  position: absolute;
  filter: blur(5px);
}

@keyframes glow-swipe {
  0% {
    clip-path: polygon(-40% 0%, 0% 0%, -40% 100%, -80% 100%);
  }
  100% {
    clip-path: polygon(150% 0%, 190% 0%, 150% 100%, 100% 100%);
  }
}