.chord-played {
  animation: chord-played 0.8s cubic-bezier(0.470, 0.000, 0.745, 0.715) both;
}

.rounded-button.chord-played,
.rounded-button.chord-played.enabled {
  border-color: rgba(47, 255, 113, 0.7);
}

@keyframes chord-played {
	0% {
    box-shadow: 0 0 0 0 rgba(47, 205, 113, 0.7),
    0 0 0px 0px rgba(47, 205, 113, 0.7);
  }
  50% {
    box-shadow: 0 0 0 0 rgba(47, 205, 113, 0.7),
    0 0 10px 3px rgba(47, 205, 113, 0.7);
  }
	70% {
    box-shadow: 0 0 10px 20px rgba(47, 205, 113, 0),
    0 0 10px 3px rgba(47, 205, 113, 0.7);
	}

	100% {
    box-shadow: 0 0 0 0 rgba(47, 205, 113, 0),
    0 0 10px 3px rgba(47, 205, 113, 0.7);
	}
}