.piano-wrapper {
  pointer-events: none;
}

.ReactPiano__Key--active,
.ReactPiano__Key--active.ReactPiano__Key--accidental {
  background-color: #0af;  /* Change the default active key color to blue */
}

.ReactPiano__Key--highlighted,
.ReactPiano__Key--highlighted.ReactPiano__Key--accidental {
  background-color: hsl(200, 90%, 85%);  /* Change the default active key color to blue */
}

.ReactPiano__Key--accidental {
  background-color: #000;  /* Change accidental keys to be completely black */
}

.ReactPiano__Key {
  transition: background-color 500ms;
}

.ReactPiano__Key--incorrect,
.ReactPiano__Key--incorrect.ReactPiano__Key--accidental {
  background-color: rgb(255, 0, 47);
}

.ReactPiano__Key--correct,
.ReactPiano__Key--correct.ReactPiano__Key--accidental {
  background-color: #6f0;
  box-shadow: 0 0 0 0 rgba(47, 205, 113, 1);
	animation: key-correct-pulse 1.25s cubic-bezier(0.33, 1, 0.68, 1);
}

@keyframes key-correct-pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(47, 205, 113, 0.7);
	}

	70% {
		box-shadow: 0 0 10px 20px rgba(47, 205, 113, 0);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(47, 205, 113, 0);
	}
}